<!-- 双折线图
 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>双折线图</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>双折线图 - 自定义折线样式</h3>
      <div id="containerGs"></div>
    </div>
    <div class="box">
      <h3>双折线图 - 阶梯折线</h3>
      <div id="containerGz"></div>
    </div>
    <div class="box">
      <h3>双折线图 - 多折线</h3>
      <div id="containerpt"></div>
    </div>
  </div>
</template>

<script>
import { DualAxes } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {
      data: [
        { year: '1991', value: 3, count: 10 },
        { year: '1992', value: 4, count: 4 },
        { year: '1993', value: 3.5, count: 5 },
        { year: '1994', value: 5, count: 5 },
        { year: '1995', value: 4.9, count: 4.9 },
        { year: '1996', value: 6, count: 35 },
        { year: '1997', value: 7, count: 7 },
        { year: '1998', value: 9, count: 1 },
        { year: '1999', value: 13, count: 20 }
      ],
      uvBillData: [
        { time: '2019-03', value: 350, type: 'uv' },
        { time: '2019-04', value: 900, type: 'uv' },
        { time: '2019-05', value: 300, type: 'uv' },
        { time: '2019-06', value: 450, type: 'uv' },
        { time: '2019-07', value: 470, type: 'uv' },
        { time: '2019-03', value: 220, type: 'bill' },
        { time: '2019-04', value: 300, type: 'bill' },
        { time: '2019-05', value: 250, type: 'bill' },
        { time: '2019-06', value: 220, type: 'bill' },
        { time: '2019-07', value: 362, type: 'bill' }
      ],
      transformData: [
        { time: '2019-03', count: 800, name: 'a' },
        { time: '2019-04', count: 600, name: 'a' },
        { time: '2019-05', count: 400, name: 'a' },
        { time: '2019-06', count: 380, name: 'a' },
        { time: '2019-07', count: 220, name: 'a' },
        { time: '2019-03', count: 750, name: 'b' },
        { time: '2019-04', count: 650, name: 'b' },
        { time: '2019-05', count: 450, name: 'b' },
        { time: '2019-06', count: 400, name: 'b' },
        { time: '2019-07', count: 320, name: 'b' },
        { time: '2019-03', count: 900, name: 'c' },
        { time: '2019-04', count: 600, name: 'c' },
        { time: '2019-05', count: 450, name: 'c' },
        { time: '2019-06', count: 300, name: 'c' },
        { time: '2019-07', count: 200, name: 'c' }
      ]
    }
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const dualAxes = new DualAxes('container', {
      data: [data, data],
      xField: 'year',
      yField: ['value', 'count'],
      geometryOptions: [
        {
          geometry: 'line',
          color: '#5B8FF9'
        },
        {
          geometry: 'line',
          color: '#5AD8A6'
        }
      ]
    })

    dualAxes.render()

    this.containerGs()
    this.containerGz()
    this.containerpt()
  },
  methods: {
    containerGs() {
      const data = this.data
      const dualAxes = new DualAxes('containerGs', {
        data: [data, data],
        xField: 'year',
        yField: ['value', 'count'],
        geometryOptions: [
          {
            geometry: 'line',
            smooth: false,
            color: '#5B8FF9',
            label: {
              formatter: datum => {
                return `${datum.value}个`
              }
            },
            lineStyle: {
              lineWidth: 3,
              lineDash: [5, 5]
            }
          },
          {
            geometry: 'line',
            smooth: true,
            color: '#5AD8A6',
            lineStyle: {
              lineWidth: 4,
              opacity: 0.5
            },
            label: {
              formatter: datum => {
                return `${datum.count}个`
              }
            },
            point: {
              shape: 'circle',
              size: 4,
              style: {
                opacity: 0.5,
                stroke: '#5AD8A6',
                fill: '#fff'
              }
            }
          }
        ]
      })

      dualAxes.render()
    },
    containerGz() {
      const data = this.data
      const dualAxes = new DualAxes('containerGz', {
        data: [data, data],
        xField: 'year',
        yField: ['value', 'count'],
        geometryOptions: [
          {
            geometry: 'line',
            smooth: false,
            color: '#29cae4',
            stepType: 'vh'
          },
          {
            geometry: 'line',
            color: '#586bce',
            smooth: true
          }
        ]
      })

      dualAxes.render()
    },
    containerpt() {
      const uvBillData = this.uvBillData
      const transformData = this.transformData
      const dualAxes = new DualAxes('containerpt', {
        data: [uvBillData, transformData],
        xField: 'time',
        yField: ['value', 'count'],
        geometryOptions: [
          {
            geometry: 'line',
            seriesField: 'type',
            lineStyle: {
              lineWidth: 3,
              lineDash: [5, 5]
            },
            smooth: true
          },
          {
            geometry: 'line',
            seriesField: 'name',
            point: {}
          }
        ]
      })

      dualAxes.render()
    }
  },
  created() {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGs {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerpt {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGet {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerTexture {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



